.form,
.headline {
  padding: 10px;
  color: white;
}
.headline {
  margin: 0;
  margin-top: 30px;
}

.container {
  background-color: black;
  display: flex;

  align-items: center;
  flex-direction: column;
}

.button {
  width: 70px;
  height: 30px;
  padding: 0;
}

.input {
  height: 30px;
}

.movielist {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  gap: 10px;
  justify-content: space-evenly;
  margin: 0;
}

.movieContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.3s ease;
}

.movieContainer:hover {
  transform: scale(1.05); /* Lekko przybli≈ºa element podczas hover */
}
